import React, { Component } from 'react';

import styles from './input.module.scss'

class Input extends Component {
  state = {
    isFocusing: false,
  }

  inputRef = React.createRef();

  static defaultProps = {
    label: 'Label',
    hinttext: '',
  }

  _onFocus = () => {
    this.setState({ isFocusing: true })
  }

  _onBlur = () => {
    this.setState({ isFocusing: false })
  }

  componentDidMount() {
    this.props.elementref(this.inputRef.current)
  }

  render() {
    const { isFocusing } = this.state
    const haveValue = this.inputRef.current && !!this.inputRef.current.value
    return (
      <div className={styles.inputContainer}>
        <div className={styles.inputWrapper}>
          <input
            ref={this.inputRef}
            onFocus={this._onFocus}
            onBlur={this._onBlur}
            {...this.props}
          />
          <div
            className={`${styles.label} ${(isFocusing || haveValue) ? styles.focusing : ''}`}>
            {this.props.label}
          </div>
        </div>
        <div className={styles.hinttext}>{this.props.hinttext}</div>
      </div>
    );
  }
}

export default Input;